<template>
  <div class="container"></div>
</template>

<script>
import OrbitControls from "./controls/OrbitControl";
import ModelLoader from "./loaders/ModelLoader";

export default {
  mounted() {
    this.modelLoader = new ModelLoader(this.scene);
    this.scene.add(this.light);
    this.scene.add(new THREE.AmbientLight(0x999999));
    this.loadModels();
    this.initControls();
    this.animate();

    window.onresize = this.onWindowResizes;
  },
  methods: {
    loadModels() {
      this.modelLoader.load("christmasTree", [0, 0, 0], 30);

      this.modelLoader.addAll();
    },
    initControls() {
      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.enableDamping = true;
      this.controls.enableZoom = true;
      this.controls.enablePan = true;
      this.controls.rotateSpeed = 0.2;
      this.controls.autoRotate = true;
      this.controls.autoRotateSpeed = 1.0;
    },
    render() {
      this.renderer.render(this.scene, this.camera);
    },
    animate() {
      this.render();
      this.controls.update();
      requestAnimationFrame(this.animate);
    },
    onWindowResize() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.render();
      this.renderer.setSize(window.innerWidth, window.innerHeight);
    }
  },
  computed: {
    scene() {
      return new THREE.Scene();
    },
    renderer() {
      let renderer = new THREE.WebGLRenderer({ antialias: true });
      renderer.setSize(window.innerWidth, window.innerHeight);
      renderer.setClearColor(0xc71924);
      this.$el.appendChild(renderer.domElement);
      return renderer;
    },
    camera() {
      let camera = new THREE.PerspectiveCamera(
        45,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      );
      camera.position.set(0, 40, 100);
      camera.lookAt(new THREE.Vector3(0, 0, 0));
      return camera;
    },
    light() {
      let light = new THREE.PointLight(0xcccccc);
      light.position.set(0, 100, 0);
      light.castShadow = true;
      return light;
    },
    canvasElement() {
      if (this.renderer !== null) {
        return this.renderer.domElement;
      } else {
        return document.createElement("div");
      }
    }
  }
};
</script>

<style scoped>
</style>


