<template>
  <div class="wrap">
      <div class="talker-wrap">
        <Talker :message="msg"></Talker>
      </div>
      <div class="scene-wrap">
        <Scene></Scene>
      </div>
      <div class="player-wrap">
        <Player :mp3Url="musicUrl"></Player>
      </div>
    </div>
</template>
<script>
import Scene from "./components/Scene";
import Player from "./components/music/Player";
import Talker from "./components/message/Talker";

export default {
  name: "App",
  components: {
    Scene,
    Player,
    Talker
  },
  data() {
    return {
      msg: "!",
      musicUrl: "./static/music/JingleBells.mp3"
    };
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

html,
body,
.scene-wrap {
  margin: 0;
  height: 100%;
  overflow: hidden;
}

.wrap {
  position: relative;
}
.talker-wrap {
  position: absolute;
  width: 100%;
  top: 10px;
  text-align: center;
}
.player-wrap {
  position: absolute;
  left: 50%;
  bottom: 20px;
  margin-left: -150px;
}
</style>



