import OBJLoader from "./OBJLoader"
import MTLLoader from "./MTLLoader"

const onProgress = function() {}
const onError = function(e) {
	console.log(e)
}

class ModelLoader {
	constructor(scene) {
		this._scene = scene
		this._container = new THREE.Object3D()

		this.initContainer()
	}
	initContainer(){
		this._container.rotation.set(-0.3, 0, 0)
	}
	load(name, position = [0, 0, 0], scale = 20) {
		const container = this._container
		const assetPath = "./static/models"
		const mtlPath = `${assetPath}/${name}_mat.mtl`
		const objPath = `${assetPath}/${name}.obj`

		new THREE.MTLLoader().load(
			mtlPath,
			function(materials) {
				materials.preload()
				new THREE.OBJLoader().setMaterials(materials).load(
					objPath,
					function(loadedMesh) {
						loadedMesh.scale.set(scale, scale, scale)
						loadedMesh.position.set(
							position[0],
							position[1],
							position[2]
						)
						container.add(loadedMesh)
					},
					onProgress,
					onError
				)
			},
			onProgress,
			onError
		)
	}
	addAll() {
		this._scene.add(this._container)
	}
}

export default ModelLoader



